import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import _data from '../../../mock_data/data.json';
export default function MainMenu ( props ) {
    const [ path, setPath ] = useState( "" );
 

    useEffect( () => {
        setPath( window.location.href );
    } )

    function showAllDemos ( e ) {
        let demoItems = document.querySelectorAll( '.demo-item.hidden' );

        for ( let i = 0; i < demoItems.length; i++ ) {
            demoItems[ i ].classList.toggle( 'show' );
        }

        document.querySelector( '.view-all-demos' ).classList.toggle( 'disabled-hidden' );
        e.preventDefault();
    }

    return (
        <nav className="main-nav">
            <ul className="menu sf-arrows">
                <li className={ `megamenu-container` } id="menu-home">
                    <Link to={ `${process.env.PUBLIC_URL}` } >Trang chủ</Link>
                </li>
                <li className={ path.indexOf( `/shop/sidebar/list` ) > -1 ? 'active' : '' }>
                                        <Link to={ `${process.env.PUBLIC_URL}/shop/sidebar/list` } >Sản Phẩm</Link>
                                    </li>
                    {
                        _data.categories.wears.slice( 0, 3 ).map( ( item, index ) =>{
                            console.log(item)
                            return   <li className={ path.indexOf( `/shop/sidebar/${parseInt(item.id)+1}cols` ) > -1 ? 'active' : '' }>
                                        <Link to={ `${process.env.PUBLIC_URL}/shop/sidebar/${parseInt(item.id)+1}cols?categoryIndex=${item.id}` } >{item.name}</Link>
                                    </li>
                        }
                           
                 
                        )
                    }
            
                <li className={ path.indexOf( "blog/" ) > -1 ? 'active' : '' }>
                    <Link to={ `${process.env.PUBLIC_URL}/pages/about` } >Vanis Store</Link>
                     </li>
                {/* <li className={ path.indexOf( "element" ) > -1 ? 'active' : '' }>
                    <Link to={ `${process.env.PUBLIC_URL}/elements` } >Tin Tức</Link>
                </li> */}
            </ul>
        </nav>
    );
}