import React from 'react';
import { Link } from 'react-router-dom';

import { mobileMenu } from '../../../utils';
import _data from '../../../mock_data/data.json';
function MobileMainNav ( props ) {
    const PUBLIC_URL = "/react/molla"

    React.useEffect( () => {
        mobileMenu();
    } )

    return (
        <nav className="mobile-nav">
            <ul className="mobile-menu">
                <li>
                    <Link to={ `${process.env.PUBLIC_URL}` }>
                        Trang chủ
                    </Link>

                    
                </li>

  
                <li >
                        <Link to={ `${process.env.PUBLIC_URL}/shop/sidebar/list` } >Sản Phẩm</Link>
                 </li>
                    {
                        _data.categories.wears.slice( 0, 3 ).map( ( item, index ) =>{
                            console.log(item)
                            return   <li >
                                        <Link to={ `${process.env.PUBLIC_URL}/shop/sidebar/${parseInt(item.id)+1}cols?categoryIndex=${item.id}` } >{item.name}</Link>
                                    </li>
                        }
                           
                 
                        )
                    }
                    <li >
                    <Link to={ `${process.env.PUBLIC_URL}/pages/about` } >Vanis Store</Link>
                     </li>
            </ul>
        </nav>
    );
}

export default MobileMainNav;