import React from 'react';
import { Link } from 'react-router-dom';

function Footer ( props ) {
    const { logoFooter = "assets/images/home/logo-footer.png" } = props;
    const { container = "container" } = props;

    return (
        <footer className="footer footer-2">
            <div className="footer-middle border-0">
                <div className={ container }>
                    <div className="row">
                        <div className="col-sm-12 col-lg-6">
                            <div className="widget widget-about">
                                <img src={ process.env.PUBLIC_URL + '/' + logoFooter } className="footer-logo" alt="Molla Logo" width="82" height="25" />
                                <p>VANIS STORE - Chuyên kinh doanh quần áo, váy đầm, balo, phụ kiện thời trang nam nữ, v.v. tại Đà Nẵng. </p>

                                <div className="widget-about-info">
                                    <div className="row">
                                        <div className="col-sm-6 col-md-4">
                                            <span className="widget-about-title">Cần trợ giúp? Gọi ngay cho chúng tôi </span>
                                            <Link to="tel:123456789">0905 246 323</Link>
                                        </div>
                                        <div className="col-sm-6 col-md-8">
                                            <span className="widget-about-title">Hỗ Trợ Thanh Toán</span>
                                            <figure className="footer-payments">
                                                <img src={ `${process.env.PUBLIC_URL}/assets/images/payments.png` } alt="Payment methods" width="272" height="20" />
                                            </figure>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="col-sm-4 col-lg-2">
                            <div className="widget">
                                <h4 className="widget-title">Thông tin</h4>

                                <ul className="widget-list">
                                    <li><Link to={ `${process.env.PUBLIC_URL}/pages/about` }>Về Vanis Store</Link></li>
                                </ul>
                            </div>
                        </div>

                        <div className="col-sm-4 col-lg-2">
                            <div className="widget">
                                <h4 className="widget-title">Dịch Vụ Khách Hàng</h4>

                                {/* <ul className="widget-list">
                                    <li><Link to="#">Thanh toán</Link></li>
                                    <li><Link to="#">Cam kết hoàn tiền!</Link></li>
                                    <li><Link to="#">Đổi trả</Link></li>
                                    <li><Link to="#">Giao hàng</Link></li>
                                    <li><Link to="#">Điều khoản sử dụng</Link></li>
                                    <li><Link to="#">Chính sách bảo mật</Link></li>
                                </ul> */}
                            </div>
                        </div>

                        <div className="col-sm-4 col-lg-2">
                            <div className="widget">
                                <h4 className="widget-title">Tài Khoản</h4>

                                {/* <ul className="widget-list">
                                    <li><Link to="#">Đăng nhập</Link></li>
                                    <li><Link to={ `${process.env.PUBLIC_URL}/shop/cart` }>Xem giỏ hàng</Link></li>
                                    <li><Link to={ `${process.env.PUBLIC_URL}/shop/wishlist` }>Sản phẩm bạn yêu thích</Link></li>
                                    <li><Link to="#">Xem đơn hàng của bạn</Link></li>
                                    <li><Link to="#">Trợ giúp</Link></li>
                                </ul> */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="footer-bottom">
                <div className={ container }>
                    <p className="footer-copyright">Copyright © { ( new Date() ).getFullYear() } Vanis Store. All Rights Reserved.</p>
                    {/* <ul className="footer-menu">
                        <li><Link to="#">Điều khoản sử dụng</Link></li>
                        <li><Link to="#">Chính sách bảo mật</Link></li>
                    </ul> */}

                    <div className="social-icons social-icons-color">
                        <span className="social-label">Mạng xã hội</span>
                        <a href="https://www.facebook.com" className="social-icon social-facebook" rel="noopener noreferrer" title="Facebook" target="_blank"><i className="icon-facebook-f"></i></a>
                        <a href="https://twitter.com" className="social-icon social-twitter" rel="noopener noreferrer" title="Twitter" target="_blank"><i className="icon-twitter"></i></a>
                        <a href="https://instagram.com" className="social-icon social-instagram" rel="noopener noreferrer" title="Instagram" target="_blank"><i className="icon-instagram"></i></a>
                        <a href="https://youtube.com" className="social-icon social-youtube" rel="noopener noreferrer" title="Youtube" target="_blank"><i className="icon-youtube"></i></a>
                        <a href="https://pinterest.com" className="social-icon social-pinterest" rel="noopener noreferrer" title="Pinterest" target="_blank"><i className="icon-pinterest"></i></a>
                    </div>
                </div>
            </div>
        </footer>
    );
}

export default React.memo( Footer );